<template>
  <form @submit.prevent="done">
    <input type="text" :placeholder="placeholder" v-model="fieldValue" autofocus="true" class="input input-tertiary" />
    <button v-if="showDoneButton" class="button alt h-fill m5-top" :disabled="!fieldValue">NEXT <i class="fas fa-arrow-right m1-left"></i></button>
  </form>
</template>

<script>
export default {
  name: 'TextPrompt',
  data () {
    return {
      fieldValue: undefined
    }
  },
  props: ['prompt'],
  computed: {
    placeholder () {
      return this.prompt.placeholder || 'type here'
    },
    showDoneButton () {
      return !['GroupPrompt', 'OptionsPrompt'].includes(this.$parent.$options.name)
    }
  },
  watch: {
    fieldValue () {
      this.$emit('change', { [this.prompt.key]: this.fieldValue })
    }
  },
  methods: {
    clear () {
      this.fieldValue = undefined
    },
    done () {
      this.$emit('done', { [this.prompt.key]: this.fieldValue })
      this.fieldValue = undefined
    }
  }
}
</script>

