<template>
  <div class="flex-column v-fill">
    <div class="flex-row p3">
      <a v-if="showBack" class="tertiary font-title" @click="back"><i class="fas fa-arrow-left m1-right"></i>BACK</a>
      <span class="flex-one"></span>
      <a class="tertiary font-title" @click="$router.back()"><i class="fas fa-times m1-right"></i>CANCEL</a>
    </div>

    <div v-if="!downloadLink" class="p3 flex-one scrolly">
      <div class="prompt-prompt">
        <h2 class="app-font light font5 m3-bottom"><value-transition :value="currentPrompt.prompt" name="slide-left" /></h2>
      </div>

      <div class="scrolly prompt-component">
        <transition name="fade-in">
          <div :is="promptComponent" :prompt="currentPrompt" @done="next"></div>
        </transition>
      </div>
    </div>

    <div v-if="downloadLink" class="p3">
      <h2 class="tertiary app-font light font4 m4-bottom">Your form is ready to be reviewed.</h2>
      <button class="button alt" @click="downloadForm"><i class="fas fa-download m1-right"></i>Review TDS Form</button>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import prompts from '@/js/forms/tds'
import promptComponents from '@/components/prompts/prompt-components'

export default {
  name: 'Disclosures',
  data () {
    return {
      formResponses: {},
      prompts: prompts,
      currentPromptIndex: 0,
      fieldValue: undefined,
      downloadLink: undefined
    }
  },
  computed: {
    ...mapGetters(['session']),
    currentPrompt () {
      return this.prompts[this.currentPromptIndex]
    },
    promptComponent () {
      return promptComponents[this.currentPrompt.type]
    },
    showBack () {
      return this.currentPromptIndex > 0
    }
  },
  inject: ['userClient'],
  methods: {
    next (responseValues) {
      this.formResponses = Object.assign(this.formResponses, responseValues)
      if (this.currentPromptIndex === this.prompts.length - 1) {
        this.submit()
      } else {
        this.currentPromptIndex++
        if (this.currentPrompt.condition) {
          if (this.currentPrompt.condition(this.formResponses) === false) {
            this.next()
          }
        }
      }
    },
    back () {
      this.currentPromptIndex--
    },
    submit () {
      const self = this
      this.userClient.submitForm(this.session.id, 'tds', this.formResponses)
        .then(url => {
          self.downloadLink = url
        })
    },
    downloadForm () {
      window.open(this.downloadLink, '_blank')
      // URL.revokeObjectURL(this.downloadLink)
      self.$router.back()
    }
  }
}
</script>

<style lang="scss" scoped>
.prompt-prompt {
  min-height: 80px;
}
.prompt-component {
  min-height: 100px;
}
</style>
